html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  width: 100%;
  min-width: 320px;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  position: relative;
  width: 100%;
  margin-bottom: 25px; }

.header__product-name {
  margin: 0;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 56px;
  line-height: 66px;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: #fffffF; }

.header__button {
  position: absolute;
  bottom: 40%;
  left: 50%;
  transform: translateX(-50%); }

@media (max-width: 767px) {
  .header__product-name {
    font-size: 36px;
    line-height: 46px; }
  .header__button {
    bottom: 20%; } }

.video {
  width: 100%;
  max-width: 100vw;
  height: auto; }

.main {
  width: 100%;
  max-width: 1300px;
  height: 100%;
  padding: 0 80px;
  margin: 0 auto; }

@media (max-width: 1023px) {
  .main {
    padding: 0 40px; } }

@media (max-width: 767px) {
  .main {
    padding: 0; } }

.button {
  padding: 15px;
  border-radius: 50px;
  text-decoration: none;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  outline: none;
  cursor: pointer; }
  .button--header {
    min-width: 300px;
    border: 2px solid #fffffF;
    color: #fffffF;
    background-color: rgba(255, 165, 0, 0.8); }
    .button--header:hover, .button--header:focus {
      background-color: orange; }
  .button--order {
    border: none;
    color: #000000;
    background-color: #ff0000; }
    .button--order:hover, .button--order:focus {
      background-color: #ffff00; }

@media (max-width: 767px) {
  .button {
    padding: 10px; }
    .button--header {
      min-width: 150px; } }

.slider {
  position: relative;
  overflow: hidden;
  width: 100%; }

.slider__wrapper {
  position: relative;
  overflow: hidden; }

.slider__list {
  display: flex;
  transition: transform 0.6s ease;
  padding: 0;
  margin: 0;
  list-style: none; }

.slider__item {
  position: relative;
  flex: 0 0 100%;
  max-width: 100%; }

.slider__item img {
  width: 100%;
  height: 100%;
  object-position: 50% 50%; }

.slider__item p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  border-radius: 5px;
  font-size: 32px;
  line-height: 42px;
  text-align: center;
  white-space: nowrap;
  color: #fffffF;
  background-color: rgba(255, 165, 0, 0.6); }

.slider__item br {
  display: none; }

.slider__control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: none;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 50px;
  border: none;
  color: #fffffF;
  text-align: center;
  opacity: 0.5;
  background-color: transparent;
  cursor: pointer; }
  .slider__control::before {
    content: '';
    display: inline-block;
    vertical-align: top;
    width: 20px;
    height: 20px;
    background: transparent no-repeat center center;
    background-size: 100% 100%; }
  .slider__control:hover, .slider__control:focus {
    outline: 0;
    text-decoration: none;
    color: #fffffF;
    opacity: 0.9; }

.slider__control--show {
  display: flex; }

.slider__control--prev {
  left: 0; }
  .slider__control--prev::before {
    background-image: url("../img/left-arrow.svg"); }

.slider__control--next {
  right: 0; }
  .slider__control--next::before {
    background-image: url("../img/right-arrow.svg"); }

.slider__indicators {
  position: absolute;
  right: 0;
  bottom: 10px;
  left: 0;
  z-index: 15;
  display: flex;
  justify-content: center;
  padding-left: 0;
  margin-right: 15%;
  margin-left: 15%;
  list-style: none;
  margin-top: 0;
  margin-bottom: 0; }

.slider__indicators li {
  box-sizing: content-box;
  flex: 0 1 auto;
  width: 30px;
  height: 4px;
  margin-right: 3px;
  margin-left: 3px;
  text-indent: -999px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.5);
  background-clip: padding-box;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent; }

.slider__indicators li.active {
  background-color: #fffffF; }

.img-fluid {
  display: inline-block;
  height: auto;
  max-width: 100%; }

@media (max-width: 767px) {
  .slider__item p {
    font-size: 22px;
    line-height: 32px; }
  .slider__item br {
    display: inline; } }

.features {
  margin-bottom: 90px; }

@media (max-width: 767px) {
  .features {
    margin-bottom: 30px; } }

.qualities {
  margin-bottom: 90px; }

.qualities__heading {
  margin: 0;
  margin-bottom: 50px;
  font-size: 42px;
  line-height: 52px;
  text-align: center;
  color: #ffa500; }

.qualities__list {
  display: flex;
  justify-content: space-between;
  padding: 0 50px;
  margin: 0;
  list-style: none; }

.qualities__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: calc(100% / 3);
  min-height: 150px;
  padding: 30px;
  border: 1px solid rgba(0, 121, 240, 0.2);
  border-radius: 10px;
  text-align: center;
  background-color: #fffffF;
  user-select: none; }
  .qualities__item:not(:last-child) {
    margin-right: 30px; }
  .qualities__item:hover {
    box-shadow: 0 0 11px rgba(0, 121, 240, 0.2); }

.qualities__item p {
  width: 100%;
  margin: 0;
  margin-bottom: 30px; }

.qualities__item:hover svg {
  fill: #ffa500; }

@media (max-width: 1023px) {
  .qualities__list {
    padding: 0 30px; }
  .qualities__item svg {
    height: 40px;
    width: 40px; } }

@media (max-width: 767px) {
  .qualities {
    margin-bottom: 50px; }
  .qualities__heading {
    margin-bottom: 20px;
    font-size: 26px;
    line-height: 36px; }
  .qualities__list {
    width: auto;
    padding: 0 15px;
    flex-direction: column; }
  .qualities__item {
    width: auto;
    min-height: 100px;
    padding: 15px; }
    .qualities__item:not(:last-child) {
      margin-right: 0;
      margin-bottom: 20px; }
  .qualities__item p {
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 20px; }
  .qualities__item svg {
    height: 30px;
    width: 30px; } }

.order {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 45px 45px 80px 45px;
  border-radius: 10px 10px 0 0;
  background-color: #ffa500; }

.order__heading {
  align-self: flex-start;
  margin: 0;
  margin-bottom: 30px;
  font-size: 42px;
  line-height: 52px; }

.order__container {
  display: flex;
  align-items: stretch;
  width: 100%;
  margin-bottom: 20px; }

.order__left {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  margin-right: 50px; }

.order__form {
  width: 40%; }

.order__price-wrap {
  width: 100%; }

.order__price {
  display: block;
  text-align: center;
  margin: 0;
  font-weight: bold; }
  .order__price--new {
    margin-bottom: 15px;
    font-size: 56px;
    line-height: 66px;
    color: #ff0000; }
  .order__price--old {
    font-size: 36px;
    line-height: 46px;
    text-decoration: line-through;
    color: #000000; }

.order__text {
  margin: 0;
  text-transform: uppercase;
  text-align: center;
  color: #fffffF; }

.order__offer-text {
  margin: 0;
  margin-top: auto;
  color: #000000; }

.order__button {
  margin-bottom: 15px; }

.order__timer {
  text-align: center; }

.order__timer p {
  margin: 0;
  margin-bottom: 20px;
  font-size: 46px;
  line-height: 56px;
  font-weight: bold;
  text-transform: uppercase;
  color: #ff0000; }

@media (max-width: 1023px) {
  .order__container {
    flex-direction: column;
    align-items: center; }
  .order__heading {
    width: 100%;
    text-align: center; }
  .order__left {
    width: 100%;
    margin-right: 0;
    margin-bottom: 35px; }
  .order__form {
    width: 100%;
    max-width: 600px; } }

@media (max-width: 767px) {
  .order {
    padding: 25px 15px; }
  .order__heading {
    margin-bottom: 20px;
    font-size: 26px;
    line-height: 36px; }
  .order__product img {
    width: 165px;
    height: 115px; }
  .order__price--old {
    font-size: 26px;
    line-height: 36px; }
  .order__price--new {
    font-size: 46px;
    line-height: 56px; }
  .order__text,
  .order__offer-text {
    font-size: 14px;
    line-height: 24px; }
  .order__timer p {
    font-size: 36px;
    line-height: 46px; }
  .order input {
    padding: 10px; } }

.tooltip {
  position: absolute;
  bottom: calc(100% - 25px);
  right: 0;
  padding: 0 5px;
  border-radius: 3px;
  z-index: 1000;
  display: none;
  vertical-align: top;
  background-color: #ffa500; }
  .tooltip::before {
    content: "";
    position: absolute;
    z-index: 1001;
    bottom: -5px;
    border: 5px solid transparent;
    border-bottom-width: 0;
    border-top-color: #ffa500; }
  .tooltip::after {
    bottom: 100%; }
  .tooltip::before, .tooltip::after {
    left: 50%;
    transform: translateX(-50%); }

.countdown {
  display: inline-block;
  vertical-align: top;
  text-align: center;
  color: #fffffF; }

.countdown__number {
  display: inline-block;
  vertical-align: top;
  min-width: 70px;
  padding: 10px;
  border-radius: 3px;
  background-color: rgba(0, 0, 0, 0.6); }

.countdown__time {
  display: inline-block;
  vertical-align: top;
  padding: 15px;
  font-weight: bold;
  font-size: 24px;
  border-radius: 3px;
  background-color: #ff0000; }

.countdown__text {
  display: block;
  padding-top: 5px;
  font-size: 16px; }

.form {
  display: flex;
  flex-direction: column;
  padding: 35px;
  border-radius: 10px;
  background-color: #fffffF; }
  .form:hover {
    box-shadow: 0 0 11px rgba(0, 121, 240, 0.2); }

.form__field {
  position: relative;
  display: flex;
  flex-direction: column-reverse; }
  .form__field:not(:last-of-type) {
    margin-bottom: 25px; }
  .form__field:last-of-type {
    margin-bottom: 45px; }

.form input {
  position: relative;
  padding: 15px;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px; }
  .form input:hover {
    border: 2px solid rgba(0, 121, 240, 0.4);
    outline: none; }
  .form input:focus {
    border: 2px solid rgba(0, 121, 240, 0.8);
    outline: none; }
  .form input::placeholder {
    color: rgba(0, 0, 0, 0.3); }

.form input:focus ~ .tooltip {
  display: inline-block; }

.form label {
  margin-bottom: 5px;
  font-weight: bold; }
